import React,{Component, useEffect, useState} from 'react'
import ProductDetail from '../Components/ProductDetail'
import firebase from '../Config/Firebase'
import IButton from '../Components/IButton'
import { Link } from 'react-router-dom'

function Details(props) {
  const id = props.match.params.id
  const [product, setProduct] = useState({})
  const [loading, setLoading] = useState(true)
  const [buttonText, setButtonText] = useState("SAVE CHANGES")
  const [buttonColor, setButtonColor] = useState({backgroundColor:"#0088a9"})
  const [form, setForm] = useState({name:'', price:'', description:'', image:''})
  const [visibility, setVisibility] = useState({opacity:0})
  const [hidden, setHidden] = useState(true)

  useEffect(
    ()=>{
      firebase.db.doc('products/'+id)
      .get()
      .then(doc=>{
        setProduct(doc)
        setLoading(false)
        setForm({
          name:doc.data().name, 
          description:doc.data().description, 
          price:doc.data().price, 
          image:doc.data().image, })
      })
    },
    []
  )

  const handleChange = (e) => {
    const name = e.target.name
    const value = e.target.value
    console.log(name, value)
    setForm({...form,[name]:value})
  }

  const handleSumbit = (e) => {
    e.preventDefault()
    firebase.db.doc("products/"+id)
    .set({
      name:form.name,
      description:form.description,
      price:form.price,
      image:form.image,
    },{merge:true})

    .then(doc=>{
      console.log("Doc", doc)
      setButtonColor({backgroundColor:"green"})
      setButtonText("CHANGES SAVED")
    })

    .catch(error=>{
      console.log("Error", error)
      setButtonColor({backgroundColor:"rgb(136, 0, 0)"})
      setButtonText("CHANGES WEREN'T SAVED")
    })
  }

  const handleDelete = (e) => {
    e.preventDefault()
    firebase.db.doc("products/"+id)
    .delete()
    .then(doc=>{
      console.log("Doc", doc)
    })
    .catch(error=>{
      console.log("Error", error)
    })
  }

  const showEditCard = () => {
    if (hidden) {
      setVisibility({opacity:1})
      setHidden(false)
    } else {
      setVisibility({opacity:0})
      setHidden(true)
    }
    
  }
  

  if (loading) {
    return(
      <p className="loader">cargando...</p>
    )
    
  }
  else {
    return(
      <div className="details">
        <p className="prod-detail_title">Product detail</p>
        <ProductDetail data={product.data()} />

        <div className="buttonWico_card">
          <IButton text="Add" icon="fas fa-plus-circle" click={handleDelete}/>
          <IButton text="Edit" icon="fas fa-edit" click={showEditCard} />
          <IButton text="Delete" icon="fas fa-minus-circle" click={handleDelete}/>
        </div>

        <div className="edit-detail_card" style={visibility}>
          <div className="edit-detail_img">
            <label>Image (Enter the URL)</label>
            <input type="text" name="image" value={form.image} onChange={handleChange} className="edit_input" placeholder="Image &#10;(Enter the URL)"></input>
          </div>
          <div className="edit-detail_text">
            <form onSubmit={handleSumbit} className="edit_form">
                <input type="text" name="name" value={form.name} onChange={handleChange} className="edit_input" placeholder="Name"></input>
                <input type="text" name="description" value={form.description} onChange={handleChange} className="edit_input" placeholder="Description"></input>
                <input type="number" min="1" name="price" value={form.price} onChange={handleChange} className="edit_input" placeholder="Price"></input>
                <button type="sumbit" className="save" style={buttonColor}>{buttonText}</button>
            </form>
          </div>
        </div>
      </div>

      
      
    )
  }
}

export default Details; 