import React,{Component, useState} from 'react'
import {Link} from 'react-router-dom'
import firebase from '../Config/Firebase'

function Signup (){
  const [form, setForm] = useState({name:'', lastname:'', email:'', pass:'', cpass:''})
  const handleSumbit = (e) => {
    e.preventDefault()
    console.log(form)
    firebase.auth.createUserWithEmailAndPassword(form.email, form.pass)
    .then(data=>{
      console.log('Registro', data.user.uid)
      firebase.db.collection('users').add({
        name: form.name,
        lastname: form.lastname,
        email: form.email,
        userId: data.user.uid
      })
      .then(data=>{
        console.log(data)
      })
      .catch(error=>{
        console.log('Error add', error)
      })
    })
    
    .catch(error=>{
      console.log('Error', error)
    })
  }

  const handleChange = (e) => {
    const name = e.target.name
    const value = e.target.value
    console.log(name, value)
    setForm({...form,[name]:value})
  }


  return(
    <div className="signup">
      <div className='log_card'>
        <p className="log_title">Sign up</p>

        <form className="log_form" onSubmit={handleSumbit}>

          <div className="log_field">
            <input type='text' value={form.name} onChange={handleChange} placeholder="Name" name="name" required></input>
            <div className="underline"></div>
          </div>

          <div className="log_field">
            <input type='text' value={form.lastname} onChange={handleChange} placeholder="Last name" name="lastname" required></input>
            <div className="underline"></div>
          </div>

          <div className="log_field">
            <input type='email' value={form.email} onChange={handleChange} placeholder="Email" name="email" required></input>
            <div className="underline"></div>
          </div>

          <div className="log_field">
            <input type='password' value={form.pass} onChange={handleChange} placeholder="Password" name="pass" required></input>
            <div className="underline"></div>
          </div>

          <div className="log_field">
            <input type='password' value={form.cpass} onChange={handleChange} placeholder="Confirm password" name="cpass" required></input>
            <div className="underline"></div>
          </div>

          <button type="submit" className="log_button">Continue</button>
          
        </form>
      </div>
    </div>
  )
    
   
}


export default Signup; 