import React,{Component} from 'react'

function IButton(props){
  const {icon, text, click} = props

  return(
    <div className="buttonWico" onClick={click} >
      <i className={icon+" buttonWico_icon"}></i>
      <p className="buttonWico_text">{text}</p>
    </div>
  )
}


export default IButton; 