import React,{Component, useState, useEffect} from 'react'

function ProductDetail(props){
  const {data} = props
  const id = props.id
  const {price,name,image,description,stock,sales} = props.data
  const [buttonText, setButtonText] = useState("PURCHASE")
  const [buttonColor, setButtonColor] = useState({backgroundColor:"#0088a9"})

  const updateButton = () => {
    if (stock < 1) {
      setButtonText("NO STOCK")
      setButtonColor({backgroundColor:"rgb(136, 0, 0)"})

    }
    else {
      setButtonText("PURCHASED")
      setButtonColor({backgroundColor:"green"})
    }
  }

  return( 
    <div className="prod-detail_card">
      <div className="prod-detail_img">
        <img src={image}></img>
      </div>
      
      <div className="prod-detail_text">
        <p className="prod-detail_sales">{sales} sold</p>
        <p className="prod-detail_name">{name}</p>
        <p className="prod-detail_description">{description}</p>
        <p className="prod-detail_price">US$ {price}</p>
        <button className="prod-detail_button" onClick={updateButton} style={buttonColor}>{buttonText}</button>
        <p className="prod-detail_stock">({stock} units left)</p>
      </div>
      
    </div>
  )
}


export default ProductDetail; 